  border-radius: 3px;
}
::-webkit-scrollbar-thumb:hover {
  background: #c9a082;
}
html {
  scroll-behavior: smooth;
}
button:focus-visible,
input:focus-visible,
textarea:focus-visible {
  outline: 2px solid #d8b4a0;
